}

@-webkit-keyframes slidein {
  0% {
    -webkit-transform: rotate(-3deg);
            transform: rotate(-3deg);
  }

  33%{
    -webkit-transform: rotate(3deg);
            transform: rotate(3deg);
  }

  66%{
    -webkit-transform: rotate(-3deg);
            transform: rotate(-3deg);
  }

  100% {
    -webkit-transform: rotate(3deg);
            transform: rotate(3deg);
  }
}

@keyframes slidein {
  0% {
    -webkit-transform: rotate(-3deg);
            transform: rotate(-3deg);
  }

  33%{
    -webkit-transform: rotate(3deg);
            transform: rotate(3deg);
  }

  66%{
    -webkit-transform: rotate(-3deg);
            transform: rotate(-3deg);
  }

  100% {
    -webkit-transform: rotate(3deg);
            transform: rotate(3deg);
  }
}

