  line-height: var(--line-height-b1);
  font-family: var(--font-secondary);
}

.found__items-quantity{
  font-weight: var(--s-medium);
  font-size: var(--h5);
  font-family: var(--font-secondary);
  line-height: 1.3;
  color: var(--color-heading);
  min-width: 120px;
  text-align: center;
}

.direction{
  font-family: var(--font-awesome);
  width: 25px;
  height: 25px;
  cursor: pointer;
  transition: all var(--transition);
  margin-left: -10%;
}

.direction__icon{
  font-family: var(--font-awesome);
}

.empty-found-page{
  width: 100%;
  text-align: center;
  margin-top: 40px;
  margin-bottom: 44px;
  color: var(--color-dark);
  font-family: var(--font-secondary);
  font-size: var(--h3);
  font-weight: var(--s-bold);
  line-height: 1.3;
}

@media (max-width: 1200px) {
  .direction{
    margin-left: 0;
  }
}

@media (max-width: 1000px) {
  
  .found__filters-block{
    top: 1em;
  }
}
.section__not-found{
  flex: 1 0 auto;
}

.not-found{
  margin-top: 3vw;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.not-found__left-side{
  flex: 1 0;
  width: 50%;
  padding-right: 80px;
}

.not-found__right-side{
  flex: 1 0;
  width: 50%;
  padding-left: 20px;
}

.arrow-right-found{
  font-family: var(--font-awesome);
  font-weight: 400;
  padding-left: 5px;
}

.title{
  font-weight: var(--s-bold);
  font-size: var(--h1);
  line-height: 1.3;
  color: var(--color-heading);
  word-break: break-word;
}

.not-found__text{
  margin-bottom: 45px;
  font-size: var(--font-size-b1);
  line-height: var(--line-height-b1);
  font-weight: var(--p-regular);
  color: var(--color-body);
}

.not-found__title-highlighter{
  color: var(--color-secondary);
  font-size: 14px;
  font-weight: var(--s-bold);
  line-height: 1;
  margin-bottom: 10px;
}

.not-found__image{
  vertical-align: middle;
  max-width: 100%;
  height: auto;
}
.section__card{
  flex: 1 0 auto;
  background-color: #f9f3f0;
}

.breadcrumbs{
  display: flex;
  flex-direction: row;
  grid-gap: 0.5em;
  gap: 0.5em;
  padding-top: 30px;
  text-transform: capitalize;
}

.fa-angle-right{
  font-family: var(--font-awesome);
}

.card__form{
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  grid-gap: 2em;
  gap: 2em;
  padding-top: 30px;
  padding-bottom: 40px;
}

.card__pictures{
  flex: 0 0 auto;
  width: 45%;
}

.card__pictures__main{
  border-radius: 10px;
  overflow: hidden;
  width: 100%;
  padding-bottom: 80%;
  margin-bottom: 20px;
  display: flex;
  border: 2px solid white;
  position: relative;
}

.picture__main{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  background-position: 50% 50%;
  background-size: cover;
  border-radius: 6px;
}

.card__pictures__thumbnails{
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  height: 20%;
  grid-column-gap: 0.5em;
  -webkit-column-gap: 0.5em;
          column-gap: 0.5em;
}

.card__pictures_thumbnail{
  border-radius: 10px;
  overflow: hidden;
  width: 20%;
  height: 80%;
  margin-bottom: 20px;
  border: 2px solid white;
  position: relative;
  cursor: pointer;
}

.pictures__thumbnail{
  width: 100%;
  height: 100%;
  position: absolute;
  object-fit: cover;
  border-radius: 6px;
}

.card__info{
  display: flex;
  flex-direction: column;
  grid-row-gap: 10px;
  row-gap: 10px;
  font-family: var(--font-secondary);
  flex: 0 0 auto;
  width: 50%;
}

.card__title{
  text-transform: capitalize;
  font-size: var(--h2);
  margin-top: 20px;
  margin-bottom: 20px;
}

.card__price{
  font-size: 1.5em;
  margin-bottom: 10px;
  font-weight: 500;
}

.card__rating{
  border-bottom: 2px solid #e9e9e9;
  padding-bottom: 20px;
  margin-bottom: 20px;
}

.card__meta{
  list-style: none;
  margin: 0;
  padding: 0;
  color: var(--color-primary);
  font-weight: 500;
}

.card__meta__item::before{
  padding-right: 15px;
  content: '\F00C';
  font-family: var(--font-awesome);
}

.description__title{
  color: var(--color-primary);
  margin-bottom: 1em;
  font-weight: bold;
  font-size: var(--h4);
  font-family: var(--font-secondary);
}

.description{
  font-size: var(--font-size-b1);
  line-height: var(--line-height-b1);
  font-weight: var(--p-regular);
  color: var(--color-body);
  margin: 0 0 30px;
}

.card__category__title{
  font-weight: var(--s-bold);
  margin-bottom: 1em;
  font-family: var(--font-secondary);
  line-height: 1.3;
  color: var(--color-heading);
}

.card__buttons{
  display: flex;
  flex-direction: row;
  grid-gap: 2em;
  gap: 2em;
}

.card__buttons .button{
  max-width: 200px;
  width: 100%;
}

.product-card__rating{
  display: flex;
  align-items: baseline;
  margin-bottom: 10px;
}

.product-card__body{
  position: relative;
  color: var(--color-border-light);
}

.product-card__body::before{
  content: '\F005\F005\F005\F005\F005';
  font-family: var(--font-awesome);
  display: block;
}

.product-card__active{
  position: absolute;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
}

.product-card__active::before{
  content: '\F005\F005\F005\F005\F005';
  font-family: var(--font-awesome);
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  color: #ffdc60;
}

.product-card__rating-text{
  padding-left: 5px;
  line-height: 24px;
  color: var(--color-body);
  transition: var(--transition);
}

@media (max-width: 1200px) {
  .card__title{
    font-size: var(--h3);
    margin: 0;
    margin-top: 10px;
    margin-bottom: 10px;
  }

  .description__title{
    font-size: var(--h5);
    margin: 0;
    margin-top: 15px;
    margin-bottom: 10px;
  }

  .description{
    margin-bottom: 10px;
  }
  
  .card__info{
    grid-row-gap: 0;
    row-gap: 0;
  }
}
